@import '~terriajs-variables';
@import '../../Sass/common/mixins';

.map__inner {
  display: table;
  width: 100%;
  height: 100%;

  * {
    box-sizing: border-box;
  }
}

.map__row {
  display: table-row;

  &:first-child {
    height: 100%;
    position: relative;
  }
}

.map__cell {
  display: table-cell;
  position: relative;
  width: 100%;

  @media print {
    display: block;
  }
}

.map-cell-map{
  @media print{
    min-height: 80vh;
  }
}

.map-wrapper {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 0;

  @media print {
    div, aside{
      height: auto !important;
    }
  }
}

@include empty-module("feedback");

.location-distance {
  composes: clearfix from '../../Sass/common/_base.scss';
  display: none;
  @media screen and (min-width: $sm) {
    display: block;
  }
  position: absolute;
  bottom: 2px;
  right: 3px;
  z-index: 1;
}

.feedback-button-wrapper {
  position: absolute;
  bottom: 100px;
  right: $padding*2;
  z-index: 0;
}

.print-disclaimer {
  display: none;
}

@media print {
  .print-disclaimer {
    display: block;
    width: 100%;
    clear: both;
  }
}



